@import url(https://fonts.googleapis.com/css2?family=Pangolin&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body, p, button {
  font-family: 'Pangolin', cursive;
}

.main {
  width: 100vw;
  height: calc(100vh - 60px);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: gainsboro;
}

.page_zero {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 30px;
}

.button_start {
  font-size: 30px;
  background-color: tomato;
  color: white;
  cursor: pointer;
  padding: 15px;
  border: 0;
  border-radius: 30px;
  box-shadow: 0px 5px 10px 2px rgba(236, 131, 31, 0.2);
}

.button_start:hover {
  background-color: rgb(251, 130, 109);
}

.menu {
    width: 100vw;
    height: 60px;
    background: #222;
    color: floralwhite;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.sound_button {
    padding: 0;
    border: 0;
    background-color: #222;
    font-size: 15px;
    color: white;
    padding-left: 30px;
}

.sound_icon {
    margin-right: 10px;
}

.sound_on {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.sound_off {
    display: flex;
    flex-direction: row;
    align-items: center;
    color: gray;
}

.lang_button {
    padding: 0;
    border: 0;
    background-color: #222;
    font-size: 15px;
    color: white;
    display: flex;
    flex-direction: row;
    align-items: center;
    padding-right: 30px;
}

.lang_icon {
    margin-left: 10px;
}

.pagination {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding-right: 30px;
}

.pagination_item {
    height: 30px;
    width: 30px;
    border-radius: 100%;
    background-color: #222;
    margin-right: 3px;
    margin-left: 3px;
    cursor: pointer;
    border: 1px solid whitesmoke;
}
.pagination_item_active {
    background-color: whitesmoke;
}
.pagination_item:hover {
    background-color: whitesmoke;
}
.first {
    margin: 30px;
}
